import PortfolioData from "./../data/PortfolioData";
import '../styles/Navbar.css';

const NavbarComponent = (props) => (
    <div className='navbar'>
        <a className='navlink hover-effect' href='/'>{PortfolioData.navName}</a>

        <div>
            {PortfolioData.navLinks.map(linkData => <a className='navlink hover-effect' rel='noreferrer' target='_blank' key={linkData.text} href={linkData.link}>{linkData.text}</a>)}

            <a className='navlink' href='/' onClick={(e) => {
                e.preventDefault();
                props.toggleTheme();
            }}><i className={props.theme === 'Light.css' ? 'fas fa-moon' : 'fas fa-sun'} /></a>
        </div>
    </div>
);

export default NavbarComponent;