import '../styles/Skills.css';
import PortfolioData from "../data/PortfolioData";

const SkillsComponent = () => (
    <div className='skills'>
        <h1 className='skills-title'>Skills</h1>
        <div className='hr' />

        <div className='skill-content'>
            <SkillTable title='Languages' skills={PortfolioData.skills.languages} />
            <SkillTable title='Technologies' skills={PortfolioData.skills.technologies} />
            <SkillTable title='Software' skills={PortfolioData.skills.software} />
        </div>
    </div>
);

const SkillTable = (props) => (
    <div className='skill-table'>
        <h2 className='skill-title'>{props.title}</h2>
        {props.skills.map(skill => (
            <span className='skill' key={skill.skill}><i className={skill.icon} /> {skill.skill}</span>
        ))}
    </div>
);

export default SkillsComponent;