import '../styles/Certifications.css';
import PortfolioData from "../data/PortfolioData";

const CertificationsComponent = () => (
    <div className='certs-content'>
        <h1 className='certs-title'>Certifications</h1>
        <div className='hr' />

        <div className='certs'>
            {PortfolioData.certifications.map(certification => <Certification key={certification.id} cert={certification} />)}
        </div>
    </div>
);

const Certification = (props) => (
    <div className='certification' onClick={() => window.open(props.cert.verifyLink, '_blank')}>
        <h3>{props.cert.name}</h3>
        <h5>Credential ID: <b>{props.cert.id}</b></h5>
    </div>
);

export default CertificationsComponent;