import PortfolioData from "./../data/PortfolioData";
import './../styles/Introduction.css';

const IntroductionComponent = () => (
    /*<div className='intro-div' style={{backgroundImage: `url(${PortfolioData.introBackground})`}}>*/
    <div className='intro-div'>
        <img className='profile-pic' src={PortfolioData.introImage} alt='' />

        <div className='intro-text'>
            <p className='intro-name'>{PortfolioData.introName}</p>
            {PortfolioData.introLines.map(text => <p key={text}>{text}</p>)}
        </div>
    </div>
);

export default IntroductionComponent;