import React from "react";

import NavbarComponent from "./components/NavbarComponent";
import IntroductionComponent from "./components/IntroductionComponent";
import AboutMeComponent from "./components/AboutMeComponent";
import SkillsComponent from "./components/SkillsComponent";
import ProjectsComponent from "./components/ProjectsComponent";
import CertificationsComponent from "./components/CertificationsComponent";
import ContactComponent from "./components/ContactComponent";

const lightTheme = 'Light.css';
const darkTheme = 'Dark.css'

export default class App extends React.Component {

    state = {
        theme: lightTheme
    };

    componentDidMount() {
        let theme = localStorage.getItem('theme');
        if(!theme) {
            localStorage.setItem('theme', lightTheme);
            theme = lightTheme
        }

        this.setState({theme});
    }

    toggleTheme = () => {
        const currentTheme = this.state.theme;
        let newTheme = currentTheme === lightTheme ? darkTheme : lightTheme;
        this.setState({theme: newTheme});

        localStorage.setItem('theme', newTheme);
    };

    render() {
        const {theme} = this.state;

        return (
            <>
                <link rel='stylesheet' type='text/css' href={`/assets/css/${theme}`} />
                <div className='content'>
                    <NavbarComponent theme={theme} toggleTheme={this.toggleTheme} />
                    <IntroductionComponent />
                    <AboutMeComponent />
                    <SkillsComponent />
                    <ProjectsComponent />
                    <CertificationsComponent />
                    <ContactComponent />
                </div>
            </>
        );
    }
}