import React from "react";

import '../styles/Projects.css'
import PortfolioData from "../data/PortfolioData";

export default class ProjectsComponent extends React.Component {

    state = {
        projectOpen: false,
        projectIdx: 0,
        selectedImage: 0
    };

    selectProject = (index) => {
        this.setState({projectIdx: index, selectedImage: 0, projectOpen: true});
        document.getElementById('project-view').scrollIntoView({ behavior: 'smooth' });

    };

    render() {
        const {projectOpen, projectIdx, selectedImage} = this.state;
        const project = PortfolioData.projects[projectIdx];

        return (
            <div className='projects-container'>
                <h1 className='about-title'>Projects</h1>
                <div className='hr' />

                <div className='projects'>
                    {PortfolioData.projects.map((project, index) =>
                        <ProjectComponent
                            onClick={this.selectProject}
                            index={index}
                            key={project.name}
                            project={project}
                        />
                    )}
                </div>

                <div id='project-view' className='project-view' style={projectOpen ? {maxHeight: 'unset', padding: '2rem'} : {}}>
                    <img className='selected-image' src={project.images[selectedImage]} alt='' />

                    <div className='sub-images'>
                        {project.images.map((image, idx) => (
                            <img key={image} className='sub-image' src={image} alt='' onClick={() => this.setState({selectedImage: idx})} />
                        ))}
                    </div>
                </div>
            </div>
        );
    }
}

const ProjectComponent = (props) => (
    <div className='project' onClick={() => props.onClick(props.index)}>
        <div className='project-header'>
            <h2>{props.project.name}</h2>
            <div>{props.project.icons.map(icon => <i key={icon} className={icon + ' fa-lg'} />)}</div>
        </div>

        <p className='project-desc'>{props.project.description}</p>
    </div>
);