import '../styles/Contact.css';
import PortfolioData from "../data/PortfolioData";

const ContactComponent = () => (
    <div className='contact-container'>
        <h4>If you have any questions feel free to contact me. Thanks for stopping by!</h4>

        <div className='contacts'>
            {PortfolioData.contacts.map(contact => <Contact key={contact.icon} data={contact} />)}
        </div>

        <div className='copyright'>
            <h4>&copy; {new Date().getFullYear()} {PortfolioData.fullName}</h4>
        </div>
    </div>
);

const Contact = (props) => <i className={`${props.data.icon} fa-5x contact-icon`} onClick={() => window.open(props.data.link, '_blank')} />;
export default ContactComponent;